<template>
  <Navbar
    @searchValue="search = $event"
    :lang="lang"
    @changeLang="changeLang"
    :words="words"
  />
  <Notes
    :notes="filterNotes"
    @delNote="deleteNote"
    @changeNote="changeNote"
    :lang="lang"
    :words="words"
  />
  <Modal
    v-show="modalOpen"
    @closeModal="closeModal"
    :currentId="currentId"
    @addNote="addNote"
    :edit="edit"
    :editNote="editNote"
    @editeNote="editeNote"
    :lang="lang"
    :words="words"
  />
  <AddButton @openModal="openModal" />
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
import AddButton from "@/components/AddButton.vue";
import langs from "@/lang"; // Словарь языков

export default {
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton,
  },
  data() {
    return {
      currentId: 1,
      modalOpen: false,
      notes: [],
      edit: false,
      editNote: {},
      search: "",
      lang: "ru", // текущий язык
      words: langs, // объект с языковыми словами
    };
  },

  created() {
    this.getNotes();
    this.initLang();
  },

  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },

  methods: {
    openModal() {
      this.modalOpen = true;
    },
    closeModal(status) {
      this.modalOpen = status;
      setTimeout(() => (this.edit = false), 100);
    },
    addNote(item) {
      this.notes.push(item);
      localStorage.setItem("notes", JSON.stringify(this.notes));
      this.modalOpen = false;
    },
    deleteNote(id) {
      this.notes = this.notes.filter((note) => note.id !== id);
      localStorage.setItem("notes", JSON.stringify(this.notes));
    },
    getNotes() {
      const localNotes = localStorage.getItem("notes");
      if (localNotes) {
        try {
          this.notes = JSON.parse(localNotes);
        } catch (e) {
          console.error("Ошибка при разборе заметок из localStorage", e);
          this.notes = [];
        }
      }
    },

    changeNote(id) {
      this.edit = this.modalOpen = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
    },
    editeNote(noteEdited) {
      this.notes.forEach((note) => {
        if (note.id == noteEdited.id) {
          note.title = noteEdited.title;
          note.text = noteEdited.text;
          note.date = noteEdited.date;
        }
      });
    },
    changeLang(val) {
      this.lang = val;
      localStorage.setItem("lang", val); // Сохраняем выбранный язык в localStorage
    },
    initLang() {
      const storedLang = localStorage.getItem("lang");
      if (storedLang) {
        this.lang = storedLang;
      } else {
        localStorage.setItem("lang", this.lang);
      }
    },
  },

  watch: {
    notes: {
      handler(newNotes) {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>
