<template>
  <div class="notes">
    <div class="container">
      <div class="notes_top">
        <h2 class="notes_top-title">
          {{ notes.length > 0 ? words.infobar[lang] : words.noinfobar[lang] }}
        </h2>
        <button class="notes_top-btn" @click="grid = !grid">
          <img src="@/assets/images/menu.png" alt="" v-if="grid" />
          <img src="@/assets/images/grid.png" alt="" v-else />
          <span>{{ grid ? words.list[lang] : words.grid[lang] }}</span>
        </button>
      </div>
      <div class="notes_list" :class="{ active: !grid }">
        <Noteitem
          :grid="grid"
          v-for="note in notes"
          :key="note.id"
          :note="note"
          @delNote="$emit('delNote', note.id)"
          @changeNote="$emit('changeNote', note.id)"
          :lang="lang"
          :words="words"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Noteitem from "@/components/Noteitem.vue";
export default {
  components: {
    Noteitem,
  },
  props: {
    words: Object,
    lang: String,
    notes: Array,
  },
  data() {
    return {
      grid: true,
    };
  },
};
</script>
