<template>
  <div class="notes_item">
    <div class="notes_item-top" :class="{ between: !grid }">
      <h3 class="notes_item-title">{{ note.title }}</h3>
      <p class="notes_item-date">{{ note.date }}</p>
    </div>
    <p class="notes_item-text">
      {{ note.text }}
    </p>
    <div class="notes_item-btns">
      <button class="notes_item-btn edit" @click="$emit('changeNote', note.id)">
        <img src="@/assets/images/list.png" alt="" />
        <span>{{ words.editbtn[lang] }}</span>
      </button>
      <button class="notes_item-btn dell" @click="$emit('delNote', note.id)">
        <img src="@/assets/images/dell.png" alt="" />
        <span>{{ words.deledit[lang] }}</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    words: Object,
    lang: String,
    grid: Boolean,
    note: Object,
  },
};
</script>
