<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal_block" @click.stop>
        <h2 class="modal_block-title">
          {{ edit ? words.titlewindowedit[lang] : words.titlewindow[lang] }}
        </h2>
        <div class="modal_inputs">
          <label>
            <span>Title</span>
            <input
              type="text"
              placeholder="Title"
              v-model="title"
              @keyup.enter="addNote"
            />
          </label>
          <label>
            <span>Content</span>
            <textarea
              placeholder="Content"
              v-model="text"
              @keyup.enter="addNote"
            ></textarea>
          </label>
        </div>
        <div class="modal_btns">
          <button class="cancel" @click="closeModal">
            {{ words.closebtn[lang] }}
          </button>
          <button v-if="!edit" class="add" @click="addNote">
            {{ words.addbtn[lang] }}
          </button>
          <button v-else class="add" @click="changeNote">
            {{ words.editwindowbtn[lang] }}
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      text: "",
      id: this.currentId,
    };
  },
  props: {
    words: Object,
    lang: String,
    currentId: Number,
    edit: Boolean,
    editNote: Object,
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
      this.title = "";
      this.text = "";
    },
    addNote() {
      if (this.title != "" && this.text != "") {
        const item = {
          id: this.id++,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", item);
        this.title = "";
        this.text = "";
      }
    },
    changeNote() {
      if (this.title != "" && this.text != "") {
        const editeNotes = {
          id: this.editNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editeNote", editeNotes);
        this.closeModal();
      }
    },
  },
};
</script>
