<template>
  <header class="header">
    <Transition name="header_content">
      <div class="header_content" v-show="header === true">
        <button
          class="header_content-lang"
          @click="changeLang"
          v-if="lang == 'ru'"
        >
          RU
        </button>
        <button class="header_content-lang" @click="changeLang" v-else>
          UZ
        </button>
        <h1 class="header_content-title">
          {{ words.appbartitle[lang] }}
        </h1>
        <button class="header_content-search" @click="header = false">
          <img src="@/assets/images/search.png" alt="search" />
        </button>
      </div>
    </Transition>

    <Transition name="header_form">
      <div class="header_form" v-show="header === false">
        <button
          class="header_form-back"
          @click="(header = true), (search = '')"
        >
          <img src="@/assets/images/back.png" alt="back" />
        </button>
        <input
          type="text"
          class="header_form-search"
          :placeholder="words.appbarsearch[lang]"
          v-model="search"
        />
        <button class="header_form-close" @click="search = ''">
          <img src="@/assets/images/close.png" alt="close" />
        </button>
      </div>
    </Transition>
  </header>
</template>

<script>
export default {
  data() {
    return {
      header: true,
      search: "",
    };
  },
  props: {
    words: Object,
    lang: String,
  },
  watch: {
    search(val) {
      this.$emit("searchValue", val);
    },
  },
  methods: {
    changeLang() {
      this.$emit("changeLang", this.lang == "ru" ? "uz" : "ru");
    },
  },
};
</script>

